import React from 'react';
import './MusicForm.css';

function MusicForm(props) {
    return(
        <div className='row row-space'>
            <div className='col-md-12' style={{ padding: 0}}>
                <div className='musicform-nav'>
                    <h1>earch for Music
                        </h1>
                        <div>
                            <form>
                            <input>Song</input>
                            <input>Artist</input>
                            <input>Album</input>
                            </form>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default MusicForm;